import timemanager from "@/assets/js/timemanager";

export default {
    searchSchool: function (name, callback) {
        fetch('https://open.neis.go.kr/hub/schoolInfo?' + new URLSearchParams({
            KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
            Type: 'json',
            SCHUL_NM: name
        }))
        .then((response) => response.json())
        .then((rtv) => callback(rtv))
    },
    getMaxClass(school,school_grade, callback){
        fetch('https://open.neis.go.kr/hub/classInfo?' + new URLSearchParams({
            KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
            Type: 'json',
            ATPT_OFCDC_SC_CODE: school.ATPT_OFCDC_SC_CODE,
            SD_SCHUL_CODE: school.SD_SCHUL_CODE,
            GRADE: school_grade,
            AY: new Date().getFullYear()
        }))
        .then((response) => response.json())
        .then((rtv) => {
            let m = 0
            rtv['classInfo'][1]['row'].forEach(element => {
                m = Math.max(element['CLASS_NM'],m)
            })
            callback(m)
        })
    },
    getMiddleSchoolSchedule(school, school_grade, school_class, monday, callback){
        fetch('https://open.neis.go.kr/hub/misTimetable?' + new URLSearchParams({
            KEY: 'a7d9b63eda43415e8802a55fa0f4f2d2',
            Type: 'json',
            ATPT_OFCDC_SC_CODE: school.ATPT_OFCDC_SC_CODE,
            SD_SCHUL_CODE: school.SD_SCHUL_CODE,
            GRADE: school_grade,
            CLASS_NM: school_class,
            TI_FROM_YMD: timemanager.dateToStringByFormatting(monday, ''),
            TI_TO_YMD: timemanager.dateToStringByFormatting(timemanager.getCurrentWeek(monday)[4].date, ''),
            AY: new Date().getFullYear()
        }))
            .then((res) => res.json())
            .then((rtv) => {
                callback(rtv['misTimetable'][1]['row'])
            })
    }
}
