export default {
  getCurrentWeek: function (date = null) {
    let currentDay = new Date(date)
    if(date == null)currentDay = new Date()

    let theYear = currentDay.getFullYear()
    let theMonth = currentDay.getMonth()
    let theDate = currentDay.getDate()
    let theDayOfWeek = currentDay.getDay()
    let thisWeek = []
    for (let i = 1; i < 6; i++) {
      let resultDay = new Date(theYear, theMonth, theDate + (i - theDayOfWeek))
      let mm = Number(resultDay.getMonth()) + 1
      let dd = resultDay.getDate()
      mm = String(mm).length === 1 ? '0' + mm : mm
      dd = String(dd).length === 1 ? '0' + dd : dd
      thisWeek[i - 1] = {
        converted: mm + '-' + dd + '(' + this.numToDay(i) + ')',
        weekdayFormatted: this.numToDay(i),
        weekday: i,
        date: resultDay
      }
    }
    return thisWeek
  },
  numToDay: function (i) {
    if (i === 1) return '월'
    if (i === 2) return '화'
    if (i === 3) return '수'
    if (i === 4) return '목'
    if (i === 5) return '금'
  },
  leftPad: function (value) {
    if (value >= 10) {
      return value
    }
    return `0${value}`
  },
  dateToStringByFormatting: function(source, delimiter = '-') {
    const year = source.getFullYear()
    const month = this.leftPad(source.getMonth() + 1)
    const day = this.leftPad(source.getDate())
    return [year, month, day].join(delimiter)
  },
  stringToDate: function (str) {
    let year = parseInt(str.substr(0, 4))
    let month = parseInt(str.substr(4, 2))
    let day = parseInt(str.substr(6,2))
    return new Date(year,month-1,day)
  }
}
